@import "../../utils/styles/variables.scss";
@import "../../utils/styles/media.scss";

$blockName: ".container";

#{$blockName} {
  border: 1px solid $dark-blue;
  &__header {
    font-weight: bold;
    color: $dark-blue;
    background: $light-blue;
    padding: 5px;
    border-bottom: 1px solid $dark-blue;
  }

  &__nav {
    display: grid;
    grid-template-columns: auto;
    gap: 10px;
    padding: 5px;
    @include respond(medium) {
      grid-template-columns: 40% 20%;
    }
    align-items: center;
    text-align: left;
    color: $mid-blue;
    padding: 5px 10px;
    border-bottom: 1px solid $dark-blue;

    &-filter {
      text-decoration: underline;
      cursor: pointer;
      :nth-child(2) {
        border-right: 1px solid $dark-blue;
        margin: 0 5px;
        cursor: default;
      }
    }

    &-input {
      font-weight: bold;
    }
  }

  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 50px;
    color: $mid-blue;
    padding: 10px 40px;
  }

  &__modal-card {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    min-width: 100vw;
    & > img {
      max-width: 100%;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: stretch;
    border: 1px solid $dark-blue;
    &-img {
      cursor: pointer;
      border-bottom: 1px solid $dark-blue;
    }
    &-type-header {
      border-bottom: 1px solid $dark-blue;
    }
    &-header {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid $dark-blue;
      padding: 0 10px 2px;
    }
    &-category {
      font-weight: bold;
    }
    &-like-container {
      display: flex;
      gap: 20px;
    }
    &-likes {
      font-weight: bold;
      color: $orange;
    }
    &-like-text {
      cursor: pointer;
      text-decoration: underline;
    }
    &-comments {
      flex: 1;
      color: black;
      background: $light-green;
      min-height: 100px;
      list-style: none;
      text-align: left;
      padding: 0;
      margin: 0;
    }
    &-comment {
      display: flex;
      justify-content: space-between;
      padding: 5px;
      border-bottom: 1px solid $dark-blue;
      & > span:last-child {
        cursor: pointer;
      }
    }
  }

  &__no-data {
    font-weight: bold;
    padding: 10px;
  }
}
